.product-page {
    display: flex;
    justify-content: center;
    padding: 20px;
}

.product-container {
    display: flex;
    max-width: 1200px;
    width: 100%;
    background: #fff;
    border-radius: 10px;
    padding: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    align-items: center;
}

.product-image {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.product-image img {
    max-width: 80%;
    max-height: 500px;
    object-fit: cover;
    border-radius: 10px;
}

.product-details {
    flex: 1;
    padding-left: 40px;
}

.product-title {
    font-size: 28px;
    font-weight: bold;
    margin-bottom: 10px;
}

.product-price {
    font-size: 22px;
    color: red;
    margin-bottom: 10px;
}

.product-description {
    font-size: 16px;
    color: #555;
    margin-bottom: 20px;
}

.product-actions {
    display: flex;
    gap: 10px;
    margin-top: 20px;
}

.button {
    padding: 10px 20px;
    border: none;
    cursor: pointer;
    font-size: 16px;
    border-radius: 5px;
}

.add-to-cart {
    background-color: #ff4d4d;
    color: white;
}

.buy-now {
    background-color: #008CBA;
    color: white;
}
